import style from "./post.module.scss";
import { Link } from "react-router-dom";


type PostProps = {
    title: string,
    category: string,
    score: number,
    index?: number,
}

const Post = ({ title, category, score, index }: PostProps) => {
    return (
        <div className={style.post}>

            {index === undefined
                ? <h2>{title}</h2>
                : <Link to={`/challenge/${index}`}><h2>{title}</h2> </Link>
            }

            <div className={style.tag}>{score}</div>
            <div className={style.tag}>{category}</div>
        </div>
    )
}

export default Post;