import Header from "../components/Header";
import PostContent from "../components/PostContent";
import PostHeading from "../components/PostHeading"
import PostNav from "../components/PostNav";

import { useParams } from "react-router-dom";
import { useAppSelector } from "../redux/hooks";
import { useEffect, useState } from "react";


const PostPage = () => {

    const id = parseInt(useParams().id as string);

    const [title, setTitle] = useState<string>("");
    const [readme, setReadme] = useState<string | undefined>(undefined);
    const challenges = useAppSelector(state => state.challenges.challenges);

    useEffect(() => {
        if (id < 0 || id >= challenges.length) return;
        // TODO: if return here, render not-found page

        setTitle(challenges[id].name);
        setReadme(challenges[id].readme);

    }, [challenges, id])

    return (
        <div>
            <Header />

            <div className="container">
                <PostNav id={id} />
                <PostHeading category="crypto" score={50} title={title} />

                <PostContent readme={readme} />
                <PostNav id={id} />
            </div>

        </div>
    )
}

export default PostPage;